.contributions {
    padding: 50px 150px;
    font-family: "Roboto", sans-serif;
    text-align: center;
  }
  
  .contribution_title {
    padding: 30px;
    text-align: center;
  }
  
  .contribution_title h1 {
    font-weight: 300;
    font-size: 2rem;
  }
  
  .contribution_title p {
    margin: 50px 0;
    font-weight: 100;
    font-size: 1.125rem;
    line-height: 1.6;
  }
  
  .contribution_sctn {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .contribution {
    flex: 1 1 calc(33.333% - 40px);
    max-width: calc(33.333% - 40px);
    text-align: center;
    margin-bottom: 20px;
  }
  
  .contribution h1 {
    font-weight: 300;
    font-size: 3rem;
    color: #fff;
  }
  
  .contribution p {
    font-weight: 100;
    font-size: 1.125rem;
    line-height: 1.6;
  }
  
  @media (max-width: 768px) {
    .contributions {
      padding: 50px 50px;
    }
  
    .contribution {
      flex: 1 1 calc(50% - 40px);
      max-width: calc(50% - 40px);
    }
  
    .contribution_title h1 {
      font-size: 1.5rem;
    }
  
    .contribution_title p {
      font-size: 1rem;
    }
  
    .contribution h1 {
      font-size: 2.5rem;
    }
  
    .contribution p {
      font-size: 1rem;
    }
  }
  
  @media (max-width: 480px) {
    .contributions {
      padding: 50px 20px;
    }
  
    .contribution {
      flex: 1 1 100%;
      max-width: 100%;
    }
  
    .contribution_title h1 {
      font-size: 1.25rem;
    }
  
    .contribution_title p {
      font-size: 0.875rem;
    }
  
    .contribution h1 {
      font-size: 2rem;
    }
  
    .contribution p {
      font-size: 0.875rem;
    }
  }
  