.games {
    padding: 0 100px;
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;
    margin-bottom: 50px;
    font-family: "Roboto", sans-serif;
  }
  
  .game {
    text-align: center;
  }
  
  .game img{
    width: 100%;
    max-width: 347px;
    height: 347px;
  }
  
  .game p{
    width: 100%;
    max-width: 347px;
    font-weight: 100;
    font-size: 18px;
    text-align: center;
  }
  
  @media (max-width: 768px) {
    .games {
      padding: 0 50px;
      gap: 20px;
    }
  }
  
  @media (max-width: 480px) {
    .games {
      padding: 0 20px;
      gap: 10px;
    }
  
    .game img,
    .game p {
      max-width: 100%;
    }
  }
  