.home{
    padding-left: 100px;
    padding-right: 100px;
    margin-bottom: 180px;
    top: 147px;
}
.home_title{
    padding: 30px;
    font-family: "Roboto", sans-serif;
    text-align: center;
}
.home_title h1{
    font-weight: 300;
    font-size: 30px;
}
.home_img img {
    width: 100%;
    height: 500px;
}
.home_content {
    text-align: center;
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    padding: 50px;
}
.home_content p{
    font-weight: 100;
    font-size: 18px;
}
.home_btn{
    text-align: center;
}
.home_btn button{
    width: 350px;
    height: 40px;
    background-color: #fff;
    color: black;
}
  
  @media (max-width: 768px) {
    .home_title h1 {
      font-size: 24px;
    }
  
    .home_content p {
      font-size: 14px;
    }
  
    .home_btn button {
      width: 100%;
      max-width: 300px;
    }
  }
  