.contact{
    padding-left: 150px;
    padding-right: 150px;
    margin-top: 180px;
    font-family: "Roboto", sans-serif;
}
.contact_title{
    text-align: center;
}
.contact_title h1{
    font-weight: 300;
    font-size: 25px;
}
.contact_title p{
    font-weight: 100;
    font-size: 18px;
}
.contact_btns{
    text-align: center;
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 30px;
}
.contact_btns .btn-skype{
    width: 300px;
    height: 50px;
    background-color:black;
    color: #fff;
    border: 1px solid #fff;
    font-size: 15px;
}
.contact_btns .btn-contact{
    width: 300px;
    height: 50px;
    background-color: #fff;
    color: black;
    font-size: 15px;
}